export const addPermission = (
  resource_id,
  permission,
  groupsIdList,
  resource_type
) => {
  return new axios({
    url:
      baseUrl + "/" + resource_type + "s" + "/" + resource_id + "/permissions",
    data: {
      permission: permission,
      groups: groupsIdList
    },
    method: POST
  });
};

